.container {
    max-width: 1300px;
    margin: 0 auto;
    padding: 0 16px;
    position: relative;

    @include atSmall {
      max-width: 540px;
    }

    @include atSmallMedium {
      max-width: 720px;
    }
    @include atMedium {
      max-width: 860px;
    }

    @include atMediumLarge {
      max-width: 1040px;
    }

    @include atLarge {
      max-width: 1240px;
    }

    @include atExtraLarge {
      max-width: 1440px;
    }

  }
  
  .site-header .container {
    max-width: 1240px;
  }

  .container-sm {
    max-width: 540px;
  }

  .container--narrow {
    max-width: 960px;
  }
  
  section {
    display: block;
    padding: 1rem 0;

    &#content {
        @include atMedium {
        padding: 20rem 0;
        }
    }
  }

  b,strong {
    font-weight: bolder
}

